<template>
  <div>
    <ul>
      <item v-for="item in list" :key="item.bookId" :data="item"></item>
    </ul>
    <div class="settlement">
      <div class="countMoney">
        合计:
        <span style="color:#14B9C8">{{count|money}}</span>
      </div>
      <div class="button" @click="fn1">结算</div>
    </div>
    <cube-popup type="my-popup" :mask="false" ref="myPopup2">结算成功</cube-popup>
    <cube-loading :size="40" class='loading' v-show='loading'></cube-loading>
  </div>
</template>

<script>
import item from "../list/item";
export default {
    data() {
        return {
            loading: false
        }
    },
  computed: {
    list() {
      let ary = this.$store.state.collectData.map(item => {
        //  item.flag=false
        return item;
      });
      ary.sort((a, b) => {
        return parseInt(a.bookId) - parseInt(b.boodId);
      });
      return ary;
    },
    count() {
      return this.$store.state.collectData.reduce((prev, next) => {
        //  item.flag=false
        return prev + next.bookPrice;
      }, 0);
    },
    
  },
  components: {
    item
  },
  methods: {
    fn(options) {
      this.$store.commit("delC", options);
    },
    fn1() {
      
      this.showPopup("myPopup2")
    },
    showPopup(refId) {
      
      const component = this.$refs[refId];
      this.loading = true
      setTimeout(()=>{
          component.show();
          this.loading=false
            setTimeout(() => {
                component.hide();
                this.$store.state.collectData = [];
            }, 1000);    
      },2000)

     
    }
  }
};
</script>

<style lang="less" scoped>
.loading{
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%)
}
.settlement {
  width: 100%;
  height: 1.5rem;
  box-shadow: 0 1px 0 0 rgba(238, 238, 238, 0.5),
    0 0 0.1rem 0 rgba(0, 0, 0, 0.18);
  position: absolute;
  bottom: 1.5rem;
  padding: 0.4rem 0.5rem;
  box-sizing: border-box;
  .button {
    width: 2rem;
    height: 0.8rem;
    line-height: 0.8rem;
    background: #14b9c8;
    font-size: 0.28rem;
    color: white;
    text-align: center;
    border-radius: 0.2rem;
    float: right;
  }
  .countMoney {
    font-size: 20px;
    position: relative;
    top: 0.1rem;
    float: left;
  }
}
</style>